.ruag-c-jobsearch {

    &__inner {
        @include display-grid();
        @include grid-columns(20px, 4fr 8fr);
        @include grid-autoflow((autoflow: column, columns: 2, rows: 1, gap: column));
    }

    &__filters {
        max-width: 15rem;
    }

    &__list {
        margin-top: 36PX;
    }

    &__joblisting {
        display: block;
        padding: 2rem 0;
        text-decoration: none;
        border-bottom: 1PX solid $color--loblolly-gray;

        &:first-child {
            border-top: 1PX solid $color--loblolly-gray;
        }

        &-row {
            max-width: 32rem;

            &--title {
                margin-bottom: $inuit-global-spacing-unit-factor-0-5;

                & > * {
                    display: inline-block;
                }
            }

            &--facts {
                display: flex;
                align-items: baseline;
                justify-content: space-between;
            }
        }

        &-item {
            display: inline-block;
            color: $color--palesky-gray;
        }

        &-title {
            margin-bottom: 0;
            color: $color--text;
        }

        &-gender {
            margin-left: $inuit-global-spacing-unit-factor-1;
        }

        * {
            transition: color $global-transition-duration $global-transition-timing;
        }

        &:hover {
            border-bottom-color: $color--primary1;

            * {
                color: $color--black;
            }
        }
    }

}
