@include mq($until: tablet) {
    .ruag-c-navigation {

        &__button-desktop {
            display: none;
        }


        &__button-mobile {
            position: fixed;
            right: 0;
            bottom: 0;
            z-index: 70;
            width: 104PX;
            height: 64PX;
            cursor: pointer;


            &-bg {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                z-index: 50;
                fill: $color--abbey-gray;
            }


            .ruag-c-navigation__hamburger {
                right: $inuit-global-spacing-unit-factor-1-5;
                bottom: $inuit-global-spacing-unit-factor-0-5;
                color: $color--white-off1;


                &::before,
                &::after {
                    background-color: $color--white-off1;
                }
            }
        }


        &__content {
            position: fixed;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 60;

            @include flexbox;
            @include flex-dir(column);
            @include align-items(stretch);
            @include justify-content(center);
            min-height: 100%;
            padding: $inuit-global-spacing-unit-factor-2 $inuit-global-spacing-unit-factor-1;
            background-color: $color--white;
            transition:
                top      $global-transition-duration      $global-transition-timing,
                opacity ($global-transition-duration * 2) $global-transition-timing;
            will-change: transform, opacitiy;


            &--closed {
                top: 100vh;
                opacity: 0;
            }


            &--open {
                top: 0;
                opacity: 1;
            }


            &-inner {
                padding-top: 5rem;
                padding-bottom: 4rem;
            }
        }


        &__logo-mobile {
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            z-index: 50;
            box-sizing: content-box;
            height: 32PX;
            padding:
                $inuit-global-spacing-unit-factor-2
                $inuit-global-spacing-unit-factor-1
                $inuit-global-spacing-unit-factor-1
                $inuit-global-spacing-unit-factor-1;
            text-align: right;
            background-color: $color--white;
        }


        &__search {
            position: fixed;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 60;
            height: 64PX;
            padding-left: $inuit-global-spacing-unit-factor-1;
            @include inuit-font-size(25px);
            color: $color--text;
            line-height: 64PX;
            background-color: $color--white;
            border-top: 1px solid $color--porcelain-gray;


            &-link {
                position: absolute;
                height: 64PX;
                text-decoration: none;
                transition: top $global-transition-duration $global-transition-timing;

                &--off {
                    top: 100%;
                }

                &--on {
                    top: 0;
                }
            }
        }


        &__search-form {

        }


        &__back {
            position: absolute;
            height: 64PX;
            padding: 0;
            color: $color--text;
            transition: left $global-transition-duration $global-transition-timing;


            &::before {
                margin-right: $inuit-global-spacing-unit-factor-1;
                color: $color--monza-red;
                content: '\2190';
            }


            &--off {
                left: 100vw;
            }

            &--on {
                left: $inuit-global-spacing-unit-factor-1;
            }
        }


        &__submenu {
            margin: 0;
            padding: 0;
        }


        &__item {
            list-style: none;
        }


        &__link {
            display: inline-block;
            text-decoration: none;
        }


        &__link--level0 {
            @include inuit-font-size(32px);
            padding: $inuit-global-spacing-unit-factor-1-5 0;
        }


        &__item--level0 {
            width: 100%;
        }


        &__submenu-button {
            float: right;
            height: 5.5rem;
            padding: 0 0 0 $inuit-global-spacing-unit-factor-1-5;
            color: $color--monza-red;
        }


        &__submenu--level1 {
            position: absolute;
            top: 0;
            left: 100vw;
            z-index: 40;
            width: 100%;
            height: calc(100vh - 64px);
            padding:
                $inuit-global-spacing-unit-factor-8
                $inuit-global-spacing-unit-factor-1
                $inuit-global-spacing-unit-factor-4
                $inuit-global-spacing-unit-factor-1;
            overflow-y: scroll;
            background-color: $color--white;
            transition: left $global-transition-duration $global-transition-timing;


            &.ruag-c-navigation__submenu--open {
                left: 0;
            }
        }


        &__item-title--level1 {
            @include inuit-font-size(32px);
            margin-bottom: $inuit-global-spacing-unit-factor-1-5;
        }


        &__item--level1 {
            margin-bottom: $inuit-global-spacing-unit-factor-3;
        }


        &__image--level2 {
            display: none;
        }


        &__item-title--level2 {
            @include inuit-font-size(18px);
            padding: $inuit-global-spacing-unit-factor-0-5 0;


            &::after {
                margin-left: $inuit-global-spacing-unit-factor-1;
                color: $color--monza-red;
                content: '\2192';
            }
        }


        &__item-teaser-text--level2 {
            display: none;
        }

    }
}



