.ruag-c-form {

    &__row {
        @include mq($from: phablet) {
            @include display-grid();
            @include grid-columns($inuit-global-spacing-unit-factor-1, 1fr 1fr);
        }
    }

    &__title {
        @include inuit-font-size(26px);
        text-transform: uppercase;
    }

    &__fieldset {
        margin: 0;
        padding: 0;
        border: none;
    }

    &__field {
        margin-bottom: $inuit-global-spacing-unit-factor-2;
    }

    &__label,
    &__input-text {
        display: block;
        width: 100%;
    }

    &__check-label,
    &__radio-label {
        @include inuit-font-size(18px);
        @include mq($from: phablet) {
            @include inuit-font-size(22px);
        }
    }

    &__check-box {
        position: absolute;
        top: 3PX;
        left: 0;
        width: 21px;
        height: 21px;
        background-color: $color--white;
        border: 1PX solid $color--abbey-gray;

        @include mq($from: phablet) {
            top: 6PX;
        }
    }

    &__checkbox {
        position: relative;

        &.--inline {
            display: inline-block;
            margin-right: $inuit-global-spacing-unit-factor-1-5;
        }

        input {
            position: absolute;
            width: 0;
            height: 0;
            opacity: 0;

            &:checked ~ .ruag-c-form__check-box {
                border-color: $color--jellybean-green;

                &::before {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    display: block;
                    width: 7px;
                    height: 13px;
                    border-right: 2px solid $color--abbey-gray;
                    border-bottom: 2px solid $color--abbey-gray;
                    transform: translate(-50%, -60%) rotate(45deg);
                    content: '';
                }
            }
        }
    }

    &__check-label {
        padding-left: 32px;
    }

    &__radio-box {
        position: absolute;
        top: 3PX;
        left: 0;
        width: 21PX;
        height: 21PX;
        background-color: $color--white;
        border: 1PX solid $color--abbey-gray;
        border-radius: 50%;

        @include mq($from: phablet) {
            top: 6PX;
        }
    }

    &__radio {
        position: relative;

        &.--inline {
            display: inline-block;
            margin-right: $inuit-global-spacing-unit-factor-1-5;
        }

        input {
            position: absolute;
            width: 0;
            height: 0;
            opacity: 0;

            &:checked ~ .ruag-c-form__radio-box {
                border-color: $color--abbey-gray;

                &::before {
                    position: absolute;
                    top: 3px;
                    left: 3px;
                    display: block;
                    width: 13px;
                    height: 13px;
                    background-color: $color--abbey-gray;
                    border-radius: 50%;
                    content: '';
                }
            }
        }
    }

    &__radio-label {
        padding-left: 32px;
    }

    &__button {
        margin-top: $inuit-global-spacing-unit-factor-1;

        .ruag-c-button {
            padding: 0;
        }

        .webform-button {
            &--submit {
                position: relative;
                z-index: 1;
                padding: 0.5rem 2rem;
                font-size: 18PX;
                line-height: 32PX;
                background: transparent;
                border: none;
                cursor: pointer;
                -webkit-appearance: none;
                -moz-appearance: none;
            }
        }
    }

    &__label {
        display: inline-block;

        @include inuit-font-size(14px);
        line-height: 32PX;

        &::before {
            position: relative;
            top: -2PX;
            display: inline-block;
            width: 0;
            overflow: hidden;
            vertical-align: middle;
            transition: width $global-transition-duration $global-transition-timing;
            content: '\2192 \0020';
        }

        &--focus {
            color: $color--jellybean-green;
            font-weight: 600;

            &::before {
                width: 20PX;
            }
        }
    }

    &__help {
        color: $color--palesky-gray;
        @include inuit-font-size(14px);

        &.--error {
            display: none;
            color: $color--monza-red;
        }
    }

    &__input-text {
        position: relative;

        &::before {
            position: absolute;
            top: 0;
            left: 0;
            width: 0;
            height: 100%;
            background-color: $color--pelorous-green;
            transition: width $global-transition-duration $global-transition-timing;
            content: '';
        }

        &--focus {
            &::before {
                width: $inuit-global-spacing-unit-factor-0-5;
            }
        }

        input,
        textarea {
            display: block;
            width: 100%;
            padding: 5px 20px;
            background: $color--white-off2;
            border: 1PX solid $color--palesky-gray;
            border-radius: 0;
            box-shadow: none;

            @include inuit-font-size(18px);

            @include mq($from: phablet) {
                @include inuit-font-size(22px);
            }

            -webkit-appearance: none;
            -moz-appearance: none;

            &:invalid {
                border: 1PX solid $color--monza-red;

                ~ .ruag-c-form__help.--error {
                    display: block;
                }
            }

            &:focus {
                border: 1PX solid $color--pelorous-green;
                outline: none; // safari
            }

            &:not(output):-moz-ui-invalid {
                box-shadow: none; // firefox
            }
        }
    }

    &__message {
        @include inuit-font-size(16px);

        &.--error {
            color: $color--monza-red;
        }
    }

    // Quickfix for release. Might be refactored
    &--login {
        .ruag-c-button {
            input {
                position: relative;
                z-index: 1;
                font-size: 18PX;
                line-height: 32PX;
                background: transparent;
                border: none;
                cursor: pointer;
                -webkit-appearance: none;
                -moz-appearance: none;
            }
        }
    }

}



.webform-element {
    &-description,
    &-more {
        @extend .ruag-c-form__label;

        &::before {
            content: unset;
        }
    }
}
