.ruag-c-cta {
    @include joinimation();

    &__button {
        display: inline-block;
        margin-right: $inuit-global-spacing-unit-small;
        margin-bottom: $inuit-global-spacing-unit-small;

        a {
            display: block;
        }

        &--showarrow {
            .c-button span::after {
                display: inline-block;
                margin-left: $inuit-global-spacing-unit-factor-0-5;
                transform: translateX(0);
                transition: transform $global-transition-duration $global-transition-timing;
                content: '\2192';
            }
        }

        &:hover {
            &.ruag-c-cta__button--showarrow {
                .ruag-c-button span::after {
                    transform: translateX(5PX);
                }
            }
        }
    }
}
