.media {
    &--blazy {

        &.media--loading {

            @include animation(loading-indicator 1s ease-in-out infinite alternate);

            &::before {
                content: none !important;
            }

        }
    }
}

@include keyframes(loading-indicator) {
    0% {
        background-color: rgba(0, 0, 0, 0.1);
    }

    100% {
        background-color: rgba(0, 0, 0, 0.25);
    }
}
