.ruag-o-brandelement {
    $logo-height: 34PX;

    position: absolute;
    right: 0;
    bottom: 10%;
    width: 300PX;
    height: 180PX;

    &__bg {
        fill: $color--white;
    }

    &__bg--large {
        display: none;
    }

    &__bg--small {
        display: block;
    }

    &__text {
        position: absolute;
        bottom: $logo-height / 2;
        left: $logo-height / 2;
        max-width: 80%;

        @include inuit-font-size(18px);
        line-height: 1.2;
        hyphens: none;

        a {
            text-decoration: none;
        }
    }

    &__link {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 20;

        &~ .ruag-o-brandelement__text::after {
            @include linkArrow;
        }

        &:hover ~ .ruag-o-brandelement__text {
            color: $color--monza-red;

            &::after {
                @include linkArrow--hover;
            }
        }
    }

    &__logo {
        position: absolute;
        top: $logo-height / 2;
        right: $logo-height / 2;
        height: $logo-height;
    }

    @include mq($from: tablet) {
        $logo-height--desktop: 48PX;

        width: 480PX;
        height: 246PX;

        &__bg--large {
            display: block;
        }

        &__bg--small {
            display: none;
        }

        &__logo {
            top: $logo-height--desktop / 2;
            right: $logo-height--desktop /2;
            height: $logo-height--desktop;
        }

        &__text {
            @include inuit-font-size(21px);
        }
    }
}
