/* ==========================================================================
   #HEADINGS
   ========================================================================== */

/**
 * Redefine all of our basic heading styles against utility classes so as to
 * allow for double stranded heading hierarchy, e.g. we semantically need an H2,
 * but we want it to be sized like an H1:
 *
 *   <h2 class="u-h1"></h2>
 *
 */

.u-h1 {
    font-family: $font-family--default;
    @include inuit-font-size($inuit-font-size-h1--mobile, 38px);

    @include mq($from: phablet) {
        @include inuit-font-size($inuit-font-size-h1, 34px);
    }
}

.u-h2 {
    font-family: $font-family--default;
    @include inuit-font-size($inuit-font-size-h2--mobile, 36px);

    @include mq($from: phablet) {
        @include inuit-font-size($inuit-font-size-h2, 46px);
    }
}

.u-h3 {
    font-family: $font-family--default;
    @include inuit-font-size($inuit-font-size-h3--mobile, 32px);

    @include mq($from: phablet) {
        @include inuit-font-size($inuit-font-size-h3, 28px)
  }
}

.u-h4 {
    font-family: $font-family--default;
    @include inuit-font-size($inuit-font-size-h4, 1.25);
}

.u-h5 {
    font-family: $font-family--default;
    @include inuit-font-size($inuit-font-size-h5, 1.3);
}

.u-h6 {
    font-family: $font-family--default;
    @include inuit-font-size($inuit-font-size-h6);
}
