// not IE11 compatible,
// because it is a component for the RUAG Intranet
.ruag-c-tabularlist {
    &__inner {
        display: grid;
        grid-row-gap: $inuit-global-spacing-unit-factor-4;
        grid-column-gap: $inuit-global-spacing-unit-factor-3;
        grid-template-columns: repeat(4, 1fr);
    }


}
