/*
 * Writes the current breakpoint to the ::after element
 * so that it is accessible from JS
 */

@mixin screenSizeIndicator() {

    &::after {
        display: block;
        width: 0;
        height: 0;
        visibility: hidden;
        content: "";

        // see breakpoint settings in _settings.global.scss
        @include mq($from: mobile) {
            content: "mobile";
        }
        @include mq($from: phablet) {
            content: "phablet";
        }
        @include mq($from: tablet) {
            content: "tablet";
        }
        @include mq($from: desktop) {
            content: "desktop";
        }
        @include mq($from: wide) {
            content: "wide";
        }
    }

}
