/* ==========================================================================
   #HEADINGS
   ========================================================================== */

/**
 * Simple default styles for headings 1 through 6. Anything more opinionated
 * than simple font-size changes should likely be applied via classes (see:
 * http://csswizardry.com/2016/02/managing-typography-on-large-apps/).
 */

// We have all of our heading font sizes defined here. Passing these pixel
// values into our `inuit-font-size()` mixin will generate a rem-based
// `font-size` with a pixel fallback, as well as generating a `line-height` that
// will sit on our baseline grid.

$inuit-font-size-h1:          52px;
$inuit-font-size-h1--mobile:  32px;

$inuit-font-size-h2:          26px;
$inuit-font-size-h2--mobile:  24px;

$inuit-font-size-h3:          22px;
$inuit-font-size-h3--mobile:  22px;

$inuit-font-size-h4:          20px;
$inuit-font-size-h5:          15px;
$inuit-font-size-h6:          14px;


.ruag-e-heading {
    margin-top: 0;
    font-weight: normal;

    &--h1 {
        font-weight: 200;
        font-family: $font-family--default;
        @include inuit-font-size($inuit-font-size-h1--mobile, 38px);

        @include mq($from: tablet) {
            @include inuit-font-size($inuit-font-size-h1, 54px);
        }
    }

    &--h2 {
        font-family: $font-family--default;
        @include inuit-font-size($inuit-font-size-h2--mobile, 36px);
        text-transform: uppercase;

        @include mq($from: tablet) {
            @include inuit-font-size($inuit-font-size-h2, 46px);
        }
    }

    &--h3 {
        font-family: $font-family--default;
        @include inuit-font-size($inuit-font-size-h3--mobile, 32px);

        @include mq($from: tablet) {
            @include inuit-font-size($inuit-font-size-h3, 28px)
      }
    }

    &--h4 {
        font-family: $font-family--default;
        @include inuit-font-size($inuit-font-size-h4, 1.25);
    }

    &--h5 {
        font-family: $font-family--default;
        @include inuit-font-size($inuit-font-size-h5, 1.3);
    }

    &--h6 {
        font-family: $font-family--default;
        @include inuit-font-size($inuit-font-size-h6);
    }

    &--widget {
        @include inuit-font-size(14px);
        font-weight: 600;
        text-transform: uppercase;
    }
}
