.ruag-c-locations {
    &__master {
        @include joinimation();
        margin-bottom: $inuit-global-spacing-unit-factor-4;
    }

    @include mq($until: tablet) {
        .ruag-c-figure {
            margin: 0;
        }
    }

    @include mq($from: tablet) {
        &__inner {
            @include flexbox();
            @include justify-content(space-beteween);
        }


        &__master {
            flex: 6;
            margin-right: $inuit-global-spacing-unit-factor-1-5;
            margin-bottom: 0;
            padding-right: $inuit-global-spacing-unit-factor-4;
        }


        &__detail {
            flex: 6;
        }
    }

    @include mq($from: desktop) {
        &__master {
            flex: 8;
            margin-right: $inuit-global-spacing-unit-factor-1-5;
            margin-bottom: 0;
            padding-right: $inuit-global-spacing-unit-factor-4;
        }


        &__detail {
            flex: 4;
        }
    }


    &__map {
        position: relative;
    }


    &__locations {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }


    &__item {
        @include mq($until: tablet) {
            margin-left: 48px;  // because icon is placed outside of the grid, some space is needed on mobile
        }

        &--has-icon {
            position: relative;
        }

        &-icon {
            position: absolute;
            top: 4px;
            left: -48px;  // icon is placed to the right (outside) of the grid


            svg {
                height: 2rem;


                path {
                    fill: $color--primary1;
                }
            }
        }
    }


    &__location {
        $locationPointSize: 20px;


        position: absolute;
        display: block;
        width: $locationPointSize;
        height: $locationPointSize;
        /* move element half the size of the dot __location-link */
        margin-top: -($locationPointSize / 2);
        margin-left: -($locationPointSize / 2);
        padding: 0;
        text-decoration: none;
        background-color: rgba($color--monza-red, 0.4);
        border: none;
        border-radius: 100%;
        @include transition(all $global-transition-duration $global-transition-timing);
        cursor: pointer;


        &-text {
            position: absolute;
            top: $locationPointSize + 6;
            left: $locationPointSize / 2;
            z-index: 69;
            padding: 3PX 6PX 2PX 6PX;
            color: $color--primary1;
            font-weight: 600;
            white-space: nowrap;
            text-decoration: none;
            background-color: $color--white;
            opacity: 0;
            pointer-events: none;
            @include inuit-font-size(14px);
            @include transition(opacity $global-transition-duration $global-transition-timing);
            @include transform(translateX(-50%));


            &::after {
                position: absolute;
                top: -5PX;
                left: 50%;
                z-index: 1;
                width: 10px;
                height: 5px;
                margin-left: -5px;
                border-right: solid 5px transparent;
                border-bottom: solid 5px $color--white;
                border-left: solid 5px transparent;
                content: '';
            }
        }


        &--active {
            background-color: $color--primary1;
        }


        &:hover {
            .ruag-c-locations__location-text {
                opacity: 1;
            }
        }


        &:active {
            transition-duration: 100ms !important;
        }
    }




    &__facts {
        display: none;

        &--active {
            display: block;
        }
    }


    &__address {
        @include joinimation();
        margin-bottom: $inuit-global-spacing-unit-factor-1;


        &-link {
            @include joinimation();
            margin-bottom: $inuit-global-spacing-unit-factor-1-5;

            @at-root {
                .touchevents {
                    .ruag-c-locations__address-link {
                        .ruag-o-link__inner--has-arrow::after {
                            @include linkArrow('right');
                        }
                    }
                }
            }
        }
    }


    &__select {
        @include mq($from: tablet) {
            width: 75%;
        }
    }



}
