.ruag-c-card {
    text-decoration: none;

    &__figure {
        margin-bottom: 0;

        img {
            width: 100%;
        }
    }

    &__content {
        margin-top: -1PX;  // to avoid non-clickable area between image and content in some situations
        padding-top: $inuit-global-spacing-unit-factor-0-5;
    }

    &__title {
        display: flow-root;

        h3 {
            margin: 0;
        }
    }

    &__title-inner {
        margin-top: 10px;
        hyphens: none;
    }

    &__icon {
        float: left;
        width: 3rem;
        height: 3rem;
        padding: 5px;

        svg {
            width: 100%;
            height: 100%;
        }

        svg path {
            fill: $color--text;
            @include transition(fill $global-transition-duration $global-transition-timing);
        }
    }

    &--has-icon {
        .ruag-c-card__title-inner {
            margin-left: 60px;
        }

        .ruag-c-card__text {
            margin-left: 60px;
        }
    }

    &__number {
        @include joinimation();

        @include flexbox;
        @include flex-direction(column-reverse);
        margin-bottom: $inuit-global-spacing-unit-factor-0-5;

        &-inner {
            color: $color--regent-gray;
            font-weight: 200;
            @include inuit-font-size(96px);
            line-height: 1;
        }
    }

    &__table {
        width: 100%;
        @include inuit-font-size(14px);
        border-collapse: collapse;

        &-cell {
            border: solid $color--loblolly-gray;
            border-width: 1px 0;
        }

        &-label {
            color: $color--palesky-gray;
        }

        &-value {
            padding-left: $inuit-global-spacing-unit-factor-2;
        }
    }

    &--animation-simultaneously {
        @include joinimation();
    }

    &--animation-separately {
        .ruag-c-card__figure {
            @include joinimation();
        }

        .ruag-c-card__title {
            @include joinimation();
        }

        .ruag-c-card__text {
            @include joinimation((translate: 2rem));
        }

        .ruag-c-card__table {
            @include joinimation();
        }
    }
}


div.ruag-c-card {
    .ruag-o-title__inner {
        &::before {
            content: unset;
        }

        &:hover {
            color: $color--text;
        }
    }
}


a.ruag-c-card {
    cursor: pointer;

    .ruag-o-title__inner {
        @include transition(color $global-transition-duration $global-transition-timing);
    }

    .ruag-o-figure__inner {
        transform: translateZ(0);
        filter: brightness(1);
        transition: filter $global-transition-duration $global-transition-timing;
    }

    .ruag-c-card__number-inner {
        @include transition(color $global-transition-duration $global-transition-timing);
    }

    &:hover {
        .ruag-o-title__inner {
            color: $color--primary1;


            &::before {
                width: 100%;
                background-color: $color--primary1;
            }
        }

        .ruag-c-card__text {
            color: $color--black;

            @include transition(color $global-transition-duration $global-transition-timing);
        }

        .ruag-c-card__number-inner {
            color: $color--gull-gray;
        }

        .ruag-c-card__icon {
            svg path {
                fill: $color--primary1;
            }
        }

        .ruag-o-tag__icon-outline {
            fill: $color--primary1
        }

        .ruag-o-figure__inner {
            filter: brightness(1.1);
        }
    }

    // for touch devices add an arrow to the title, if card is a link
    @at-root {
        .touchevents {
            a.ruag-c-card .ruag-c-card__title .ruag-o-title__inner {
                padding-bottom:0;

                &::before {
                    content: unset;
                }

                &::after {
                    @include linkArrow('right');
                }
            }
        }
    }
}


.ruag-o-section--bg-dark {
    .ruag-c-card {
        &__number-inner {
            color: $color--loblolly-gray;
        }


        &__icon {
            svg path {
                fill: $color--text-inverse;
            }
        }
    }


    div.ruag-c-card {
        .ruag-o-title__inner {
            &:hover {
                color: $color--text-inverse;
            }
        }
    }


    a.ruag-c-card {
        &:hover {
            .ruag-c-card__text {
                color: $color--loblolly-gray;
            }
        }
    }
}

