@mixin truncate($color) {
    background: linear-gradient(135deg, $color 85%, transparent 0);

    @supports (clip-path: polygon(0% 0%)) {
        background: $color;
        transition: background $global-transition-duration $global-transition-timing;
        clip-path: polygon(0% 0%, 100% 0, 100% calc(100% - var(--triangle-size)), calc(100% - var(--triangle-size)) 100%, 0% 100%);
    }
}
