$inuit-global-baseline:              16px;
$inuit-global-baseline--mobile:      12px;

$inuit-global-spacing-unit-factor-0-5:      $inuit-global-baseline * 0.5;
$inuit-global-spacing-unit-factor-0-75:     $inuit-global-baseline * 0.75;
$inuit-global-spacing-unit-factor-1:        $inuit-global-baseline * 1;
$inuit-global-spacing-unit-factor-1-5:      $inuit-global-baseline * 1.5;
$inuit-global-spacing-unit-factor-2:        $inuit-global-baseline * 2;
$inuit-global-spacing-unit-factor-3:        $inuit-global-baseline * 3;
$inuit-global-spacing-unit-factor-4:        $inuit-global-baseline * 4;
$inuit-global-spacing-unit-factor-8:        $inuit-global-baseline * 8;

// typography
$inuit-global-font-size:             16px;
$inuit-global-line-height:           24px;
$inuit-global-font-size--mobile:     15px;
$inuit-global-line-height--mobile:   22px;

$inuit-global-font-size--mobile:     15px;
$inuit-global-line-height--mobile:   22px;


// inuit compatibility
$inuit-global-spacing-unit-factor-tiny:   0.5;
$inuit-global-spacing-unit-factor-small:  0.75;
$inuit-global-spacing-unit-factor:        1;
$inuit-global-spacing-unit-factor-large:  3;
$inuit-global-spacing-unit-factor-huge:   8;
