.ruag-c-subheading {
    margin-bottom: $inuit-global-spacing-unit-factor-2;


    &__inner {
        @include flexbox();
        @include flex-wrap(wrap);
        @include align-items(baseline);
        @include justify-content(space-between);
    }


    &__title {
        margin-top: 0;
        margin-right: 2rem;
        margin-bottom: 0;
    }


    &__link {
        padding-top: 0.85em;
        color: $color--link;
        line-height: 1;
        text-decoration: none;
        @include mq($from: desktop) {
            @include animatedUnderline;
        }

        @at-root {
            @include mq($until: desktop) {
                .touchevents {
                    a.ruag-c-subheading__link::after {
                        @include linkArrow('right');
                    }
                }
            }
        }
    }
}





