.ruag-c-footer {
    padding-top: $inuit-global-spacing-unit-factor-4;
    padding-bottom: 1px;
    background-color: $color--abbey-gray;

    &__inner {
        max-width: $global-section-max-width;
        margin: 0 auto;
        padding: 0 $global-section-side-padding;
        color: $color--text-inverse;
    }

    &__blocks {
        @include mq($from: tablet) {
            @include flexbox;
            @include justify-content(space-between);
        }
    }

    &__block {
        width: 100%;
        margin-bottom: 8 * $inuit-global-baseline--mobile;

        &:not(:last-child) {
            margin-right: $inuit-global-spacing-unit-factor-1-5;
        }

        &--no-margin-mobile {
            @include mq($until: tablet) {
                margin-bottom: $inuit-global-spacing-unit-factor-1;
            }
        }

        @include mq($from: tablet) {
            &--width2 {
                flex: 2;
            }

            &--width3 {
                flex: 3;
            }

            &--width5 {
                flex: 5;
            }

            &--width6 {
                flex: 6;
            }
        }

        &-left {
            margin-bottom: $inuit-global-spacing-unit-factor-1;

            @include mq($from: tablet) {
                float: left;
            }
        }

        &-right {
            margin-bottom: $inuit-global-spacing-unit-factor-1;

            @include mq($from: tablet) {
                float: right;
            }
        }
    }

    &__title {
        margin-bottom: $inuit-global-spacing-unit-factor-2;
        color: $color--loblolly-gray;
        @include inuit-font-size(16px);
        text-transform: uppercase;
    }

    &__link {
        margin-bottom: $inuit-global-spacing-unit-factor-1-5;

        .ruag-o-link__inner {
            color: $color--text-inverse;

            &:hover {
                color: $color--primary1;
            }
        }

        &--inline {
            margin-right: $inuit-global-spacing-unit-factor-1-5;

            &-mobile {
                display: inline-block;
                margin-right: $inuit-global-spacing-unit-factor-1-5;
                margin-bottom: $inuit-global-spacing-unit-factor-1;

                @include mq($from: tablet) {
                    display: block;
                }
            }
        }

        .ruag-o-link--active {
            color: $color--regent-gray;
            cursor: default;


            &:hover {
                color: $color--regent-gray;
            }


            &::before {
                display: none;
            }
        }
    }

    &__select {
        @include mq($from: tablet) {
            width: 80%;
        }
    }

    &__contact {
        &--selectable {
            display: none;
        }

        &--active {
            display: block;
        }
    }

    &__address {
        &-part {
            display: inline;

            @include mq($until: phablet) {
                &:not(:last-child)::after {
                    content: ',';
                }
            }

            @include mq($from: phablet) {
                display: block;
            }
        }
    }

    &__general-links {
        color: $color--loblolly-gray;

        .ruag-o-link__inner {
            color: inherit;
        }
    }

}


#block-allgemeiner-kontakt {
    .ruag-c-footer__link {
        @include inuit-font-size(28px);
    }
}
