.ruag-o-figure {

    margin: 0;
    transform: translateZ(0);

    img {
        display: block;
        height: 100%;
    }


    &__caption {
        margin-top: $inuit-global-spacing-unit-small;
        color: $color--palesky-gray;
        @include inuit-font-size(14px);
    }

}


.ruag-o-section--bg-dark {
    .ruag-o-figure__caption {
        color: $color--text-inverse;
    }
}


/* IE 11 hack */

@at-root {

    @media all and (-ms-high-contrast:none) {
        *::-ms-backdrop, .ruag-c-figure__inner .media { height: 100%; }
    }
}
