/* ==========================================================================
   #PARAGRAPH
   ========================================================================== */

$inuit-font-size-p:  16px !default;


p {
    // override browser styles
    margin: 0;
    font-family: $font-family--default;

}

.ruag-e-paragraph--lead {
    @include inuit-font-size(20px, 28px);
    font-weight: 200;

    @include mq($from: tablet) {
        @include inuit-font-size(26px, 36px);
    }
}
