.ruag-c-promo-box {
    --triangle-size: 0;
    @include joinimation();

    &__inner {
        position: relative;
        margin: auto;
        @include flexbox();
        @include flex-wrap(wrap);

        @include mq($from: tablet) {
            @include flex-wrap(nowrap);

        }
    }

    &__content {
        padding: $inuit-global-spacing-unit-factor-3 $inuit-global-spacing-unit-factor-2;
        @include mq($from: desktop) {
            padding: $inuit-global-spacing-unit-factor-3 10% $inuit-global-spacing-unit-factor-3 $inuit-global-spacing-unit-factor-2;
        }

        color: $color--white;
        @include truncate($color--monza-red);

        /* IE 11 hack */
        @at-root {

            @media all and (-ms-high-contrast:none) {
                *::-ms-backdrop, .ruag-o-promo-box__content { max-width: 70%; }
            }
        }

        p {
            @include inuit-font-size(20px);

            @include mq($from: tablet) {
                @include inuit-font-size(26px);
            }
        }
    }

    &__title {
        @include inuit-font-size(32px);

        @include mq($from: tablet) {
            @include inuit-font-size(44px);
        }
    }

    &__text {
        margin-bottom: $inuit-global-spacing-unit-factor-1-5;
    }

    &__figure {

        @include flex(0 0 100%);

        @include mq($from: tablet) {
            @include flex(0 0 30%);
        }

        /* IE 11 hack */
        @at-root {

            @media all and (-ms-high-contrast:none) {
                *::-ms-backdrop, .ruag-o-promo-box__figure { max-width: 30%; }
            }
        }
    }

    &__figure-inner {
        height: 100%;
    }

    &__image {
        height: 100%;
        object-fit: cover;
    }

    &::after {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: 20;
        display: block;
        border: var(--triangle-size) solid transparent;
        content: '';
    }
}


.ruag-o-section--bg-primary .ruag-c-promo-box::after {
    border-right-color:  $color--monza-red;
    border-bottom-color: $color--monza-red;
}

.ruag-o-section--bg-default .ruag-c-promo-box::after {
    border-right-color:  $color--white;
    border-bottom-color: $color--white;
}

.ruag-o-section--bg-bright .ruag-c-promo-box::after {
    border-right-color:  $color--loblolly-gray;
    border-bottom-color: $color--loblolly-gray;
}

.ruag-o-section--bg-dark .ruag-c-promo-box::after {
    border-right-color:  $color--abbey-gray;
    border-bottom-color: $color--abbey-gray;
}
