.ruag-c-logo {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 50;
    width: 188PX;
    height: 64PX;

    @include mq($from: tablet) {
        display: none;
    }


    &__bg {
        fill: $color--white;
    }


    &__logo {
        position: absolute;
        top: 16PX;
        right: 16PX;
        height: 32PX;
    }
}
