.ruag-c-accordion {
    //@include screenSizeIndicator;

    // Layout
    &__inner {

        @include mq($from: phablet) {
            @include flexbox();
            @include justify-content(space-between);
        }
    }

    &__tabs {
        @include mq($until: phablet) {
            position: sticky;
            top: 0;
            padding-top: $inuit-global-spacing-unit-factor-0-5;
        }

        z-index: 50;
        width: calc(100% + 40px);
        transform: translateX(-($global-section-side-padding));

        // white gradient at the sides
        &::before {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 2PX;
            left: 0;
            z-index: 20;
            content: '';
            pointer-events: none;
        }

        // bottom line
        &::after {
            position: absolute;
            bottom: 0;
            z-index: -1;
            display: block;
            width: 100%;
            height: 2PX;
            background-color: $color--loblolly-gray;
            content: '';
        }

        @include mq($from: phablet) {
            flex: 1;
            margin-right: 2rem;

            &::after {
                content: unset;
            }
        }
    }

    &__content {
        position: relative;
        width: 100%;
        margin-top: $inuit-global-spacing-unit-factor-3;
        overflow: hidden;

        @include mq($from: phablet) {
            flex: 2;
            margin-top: 0;
        }
    }


    // Tabs
    &__tablist {
        @include mq($from: phablet) {
            position: sticky;
            top: $inuit-global-spacing-unit-factor-4;

        }
        @include flexbox;
        @include align-items(flex-end);
        margin: 0;
        padding: 0 $global-section-side-padding;
        overflow-x: scroll;
        list-style: none;

        scrollbar-width: none;

        &::-webkit-scrollbar { display: none !important; }


        @include mq($from: phablet) {
            display: block;
            overflow: auto;
        }
    }

    &__tab {
        margin-left: 0;
        padding-right: $inuit-global-spacing-unit-factor-1-5;
        hyphens: none;

        span {
            @include inuit-font-size(20px);
            line-height: 1.4;
            cursor: pointer;

            @include animatedUnderline;

            &::before {
                background-color: $color--loblolly-gray;
            }
        }

        &--active {
            color: $color--primary1;

            span::before {
                width: 100%;
                background-color: $color--primary1;
            }
        }


        @include mq($from: phablet) {
            min-width: unset;
            margin-right: $inuit-global-spacing-unit-factor-3;
            margin-bottom: $inuit-global-spacing-unit-factor-3;
            margin-left: 0;
            transition:
                margin-left  $global-transition-duration,
                margin-right $global-transition-duration;

            &--active {
                margin-right: $inuit-global-spacing-unit-factor-1-5;
                margin-left: $inuit-global-spacing-unit-factor-1-5;
            }
        }

    }

    &__tabcontent {
        max-height: 0;
        overflow: hidden;
        opacity: 0;
        transition: opacity $global-transition-duration;

        &--active {
            opacity: 1;
        }
    }


    .ruag-c-wysiwyg {
        max-width: 100%;
    }


    .ruag-c-promo-box {
        @include flex-wrap(wrap);

        &__content {
            p {
                @include inuit-font-size(20px);
            }
        }

        &__title {
            @include inuit-font-size(32px);
        }

        &__figure {
            @include flex(0 0 100%);

            &-inner::before {
                padding-bottom: 50%;
            }
        }
    }


    .ruag-c-contentlist {
        // IE11 Grid
        //-ms-grid-rows: 1fr $inuit-global-spacing-unit-factor-4 1fr;

        // Others Grid
        @include contentlistPlacement(1);


        @include mq($from: tablet) {
            // IE11 Grid
            //-ms-grid-columns: 1fr $inuit-global-spacing-unit-factor-4 1fr;

            // Others Grid
            @include contentlistPlacement(2);
        }
    }


    .ruag-c-gallery {
        &__image {
            &--mobile-hidden {
                @include mq($until: tablet) {
                    display: none;
                }
            }
        }


        &__row {
            &--2items {
                grid-template:
                    'image1'   auto
                    'content1' auto
                    '.'        $inuit-global-spacing-unit-factor-2
                    'image2'   auto
                    'content2' auto
                    / 1fr;


                @include mq($from: tablet) {
                    grid-column-gap: $inuit-global-spacing-unit-factor-2;
                    grid-template:
                        'image1   image2'   auto
                        'content1 content2' auto
                        / 1fr     1fr;
                }
            }


            &--3items {
                grid-template:
                    'image1'   auto
                    'content1' auto
                    '.'        $inuit-global-spacing-unit-factor-2
                    'image2'   auto
                    'content2' auto
                    '.'        $inuit-global-spacing-unit-factor-2
                    'image3'   auto
                    'content3' auto
                    /  100%;


                @include mq($from: tablet) {
                    grid-column-gap: $inuit-global-spacing-unit-factor-2;
                    grid-template:
                        'image1   image1'   auto
                        'content1 content1' auto
                        '.      .'          $inuit-global-spacing-unit-factor-2
                        'image2   image3'   auto
                        'content2 content3' auto
                        / 1fr     1fr;
                }
            }
        }
    }
}


.ruag-o-section--bg-default {
    .ruag-c-accordion__tabs {
        background-color: $color--white;

        &::before {
            background-image: linear-gradient(to right,
                $color--white 8px,
                rgba($color--white, 0) 20px,               // dont make it
                rgba($color--white, 0) calc(100% - 20px),
                $color--white calc(100% - 8px));
        }
    }
}


.ruag-o-section--bg-bright {
    .ruag-c-accordion__tabs {
        background-color: $color--porcelain-gray;

        &::before {
            background-image: linear-gradient(to right,
                $color--porcelain-gray 8px,
                rgba($color--porcelain-gray, 0) 20px,               // dont make it
                rgba($color--porcelain-gray, 0) calc(100% - 20px),
                $color--porcelain-gray calc(100% - 8px));
        }
    }
}


.ruag-o-section--bg-dark {
    .ruag-c-accordion__tabs {
        background-color: $color--abbey-gray;

        &::before {
            background-image: linear-gradient(to right,
                $color--abbey-gray 8px,
                rgba($color--abbey-gray, 0) 20px,               // dont make it
                rgba($color--abbey-gray, 0) calc(100% - 20px),
                $color--abbey-gray calc(100% - 8px));
        }
    }
}
