/*
## Usage: ##
Always define the grid with [ display-grid() ], [ grid-columns() ] and [ grid-rows() ].
Then continue with either [ grid-autoflow() ] or [ grid-matrix() ] and [ grid-area() ].

    * example with [ grid-autoflow() ]: *
    @include display-grid();
    @include grid-columns(1.5em, 1fr 1fr 1fr);
    @include grid-rows(3em, auto auto);
    @include grid-autoflow((autoflow: column, columns: 3, rows: 2, gap: both));

    * example with [ grid-matrix() ] and [ grid-area() ]: *
    - (on the grid-container) -
    @include display-grid();
    @include grid-columns(1.5em, 1fr 1fr 1fr 1fr);
    @include grid-rows(3em, auto auto);
    $gridAreaMap: (
        row1: ('logo', 'nav', 'nav', 'nav'),
        row2: ('logo', '.', 'search', 'search')
    );
    @include grid-matrix($gridAreaMap);

    - (on the grid-items) -
    @include grid-area(logo, $gridAreaMap, true);


## API ##
[ display-grid ]

[ grid-columns($gap, $columns) ]
$gap     {string} Optional column gap
$columns {list}   Columns sizes

    * example: *
    @include grid-columns(10px, 1fr 3fr 2fr);


[ grid-rows($gap, $rows) ]
$gap  {string} Optional row gap
$rows {list}   Rows sizes

    * example: *
    @include grid-rows(10px, auto auto auto);


[ grid-autoflow($data) ]
$data {map}
$data.autoflow {string} Value or "grid-auto-flow". Either 'column' or 'row'.
$data.columns  {number} Number of columns in the grid.
$data.rows     {number} Numbers of rows in the grid.
$data.gap      {string} Where to allow for gaps. Either 'column', 'row' or 'both'.

    * example: *
    @include grid-autoflow((autoflow: column, columns: 3, rows: 2, gap: both));


[ grid-matrix($map) ]
$map {map} Map of grid area names.

    * example: (on the grid container) *
    $gridAreaMap: (
        row1: ('logo', 'nav', 'nav', 'nav'),
        row2: ('logo', '.', 'search', 'search')
    );
    @include grid-matrix($gridAreaMap);


[ grid-area($area, $map, $columnGap: false, $rowGap: false) ]
$area      {string} Name of the area.
$map       {map}    Map of grid area names.
$columnGap {bool}   Whether to use gaps for the columns.
$rowGap    {bool}   Whether to use gaps for the rows.

    * example: (on the grid items) *
    @include grid-area(logo, $gridAreaMap, true);


## Ressources ##
- gridbyexample.com (Rachel Andrews)                                               [https://gridbyexample.com/]
- Should I try to use the IE implementation of CSS Grid Layout? (Rachel Andrews)   [https://rachelandrew.co.uk/archives/2016/11/26/should-i-try-to-use-the-ie-implementation-of-css-grid-layout/]
- MDN                                                                              [https://developer.mozilla.org/en-US/docs/Learn/CSS/CSS_layout/Grids]
- CSS-Tricks                                                                       [https://css-tricks.com/snippets/css/complete-guide-grid/]
- Mortan Rand-Hendriksen on Smashing Magazine and Youtube                          [https://www.smashingmagazine.com/2017/06/building-production-ready-css-grid-layout/]  [https://www.youtube.com/watch?v=7kVeCqQCxlk]
- Post for the SCSS Grid Mixins                                                    [https://medium.com/@gisugosu/css-grid-layout-spec-2-to-spec-1-transpiler-with-sass-415dff4dd31b]
