.ruag-c-navigation {

    &__hamburger {
        position: absolute;
        z-index: 55;
        padding-top: 16PX;
        pointer-events: auto;


        &::before,
        &::after {
            position: absolute;
            left: 0;
            width: 100%;
            height: 2PX;
            transform: rotate(0);
            transform-origin: 0;
            transition:
                left      (2 * $global-transition-duration) $global-transition-timing,
                top       (2 * $global-transition-duration) $global-transition-timing,
                width     (2 * $global-transition-duration) $global-transition-timing,
                transform (2 * $global-transition-duration) $global-transition-timing;
            content: '';
        }


        &::before {
            top: 12PX;
        }


        &::after {
            top: 0;
        }


        &--open::before,
        &--open::after {
            left: 13PX;
        }


        &--open::before {
            top: 15PX;
            width: 25PX;
            transform: rotate(-45deg);
        }


        &--open::after {
            top: -3PX;
            width: 25PX;
            transform: rotate(45deg);
        }
    }

}


@import 'components.navigation--mobile';
@import 'components.navigation--desktop';

