@include mq($from: tablet) {

    .ruag-c-navigation {

        &__button-mobile,
        &__logo-mobile {
            display: none;
        }

        &__logo-desktop {
            position: absolute;
            bottom: $inuit-global-spacing-unit-factor-1;
            height: 32PX;
            overflow: hidden;
            pointer-events: auto;

            img {
                position: absolute;
                top: 0;
                right: 0;
                width: auto;
                height: 100%;
            }

            &--closed {
                right: 58px - 43px;
                width: 56PX;
                transition:
                    right $global-transition-duration $global-transition-timing $global-transition-duration,
                    width $global-transition-duration $global-transition-timing $global-transition-duration;
            }

            &--open {
                right: $inuit-global-spacing-unit-factor-3;
                width: 140PX;
                transition:
                    right $global-transition-duration $global-transition-timing,
                    width $global-transition-duration $global-transition-timing;
            }
        }

        &__button-desktop {
            position: fixed;
            top: 0;
            left: 0;
            z-index: 55;
            display: block;
            height: 64PX;
            cursor: pointer;
            pointer-events: none;

            &-bg {
                width: 100%;
                height: 100%;
                background-color: transparent;
            }

            &-trunc {
                position: absolute;
                top: 0;
                right: -43PX;
                width: 43PX;
                height: 64PX;

                path {
                    fill: $color--white;
                }
            }

            .ruag-c-navigation__hamburger {
                bottom: $inuit-global-spacing-unit-factor-0-5;
                left: $inuit-global-spacing-unit-factor-3;
                color: $color--text;

                &::before,
                &::after {
                    background-color: $color--text;
                }
            }

            &--closed {
                width: 200px;
                background-color: $color--white;
                transition:
                    width            $global-transition-duration $global-transition-timing $global-transition-duration,
                    background-color $global-transition-duration $global-transition-timing;
            }

            &--open {
                width: 100%;
                background-color: transparent;
                transition:
                    width            $global-transition-duration $global-transition-timing,
                    background-color $global-transition-duration $global-transition-timing $global-transition-duration;
            }
        }

        &__content {
            position: fixed;
            top: 0;
            right: 0;
            left: 0;
            z-index: 51;
            padding: 0 $inuit-global-spacing-unit-factor-3;
            @include inuit-font-size(18px);
            background-color: rgba($color--white, 0.95);

            @supports (backdrop-filter: blur(5px)) {
                background-color: rgba($color--white, 0.9);
                backdrop-filter: blur(10PX);
            }

            border-bottom: 1px solid $color--porcelain-gray;
            perspective: 300PX;
            opacity: 0;

            &--closed {
                opacity: 0;
                transition: opacity 0s $global-transition-timing $global-transition-duration;
                pointer-events: none;
            }

            &--open {
                opacity: 1;
                transition: opacity 0s $global-transition-timing $global-transition-duration;
                pointer-events: auto;
            }
        }

        &__search {
            position: absolute;
            top: 0;
            right: 20rem;

            &-link {
                display: block;
                padding:
                    $inuit-global-spacing-unit-factor-1-5
                    $inuit-global-spacing-unit-factor-1
                    $inuit-global-spacing-unit-factor-0-5;
                text-decoration: none;
            }
        }

        &__back {
            display: none;
        }

        &__submenu {
            margin: 0;
            padding: 0;
        }

        &__item {
            list-style: none;
        }

        &__submenu-button {
            display: none;
        }

        &__submenu--level0 {
            display: flex;
        }

        &__item--level0 {
            &:first-child {
                margin-left: $inuit-global-spacing-unit-factor-8;
            }
        }

        &__link--level0 {
            display: block;
            padding:
                $inuit-global-spacing-unit-factor-1-5
                $inuit-global-spacing-unit-factor-1
                $inuit-global-spacing-unit-factor-0-5;
            text-decoration: none;
        }

        &__submenu--level1 {
            position: absolute;
            top: 65PX;
            right: 0;
            bottom: 0;
            left: 0;
            @include display-grid();
            @include grid-columns(6rem, 2fr 1fr);
            @include grid-rows(2rem, auto auto auto auto auto);
            height: 100vh;
            padding: $inuit-global-spacing-unit-factor-3;
            background-color: $color--white;
            opacity: 0;
            transition: opacity $global-transition-duration $global-transition-timing;
            pointer-events: none;

            & > .c-navigation__item:first-child {
                -ms-grid-row: 1;
                grid-row: 1/6;
                -ms-grid-column: 1;
                grid-column: 1/2;
                -ms-grid-column-span: 6;
            }

            @for $i from 2 through 6 {
                & > .c-navigation__item:nth-child(#{$i}) {
                    -ms-grid-row: (2 * $i - 3);
                    grid-row: ($i - 1);
                    -ms-grid-column: 3;
                    grid-column: 2;
                }
            }
        }

        &__item--level0:hover {
            .ruag-c-navigation__submenu--level1 {
                opacity: 1;
                transition-duration: $global-transition-duration * 2;
                pointer-events: auto;
            }

            .ruag-c-navigation__item--level1 {
                top: 0;
                opacity: 1;

                @for $i from 1 through 6 {
                    &:nth-child(#{$i}) {
                        transition:
                            top      $global-transition-duration      $global-transition-timing ($global-transition-duration * $i),
                            opacity ($global-transition-duration * 2) $global-transition-timing ($global-transition-duration * $i);
                    }
                }
            }
        }

        &__item--level1 {
            position: relative;
            top: $global-joinimation-translate;
            font-weight: 200;
            @include inuit-font-size(36px);
            opacity: 0;
            transition:
                top      $global-transition-duration      $global-transition-timing ($global-transition-duration),
                opacity ($global-transition-duration * 2) $global-transition-timing ($global-transition-duration);
        }

        &__submenu--level2 {
            margin-top: $inuit-global-spacing-unit-factor-2;
        }

        &__item--level2 {
            @include inuit-font-size(16px);
        }

        &__link--level2 {
            text-decoration: none;
        }

        &__item--level1:not(:first-child) {
            .ruag-c-navigation__link--level2 {
                margin-bottom: $inuit-global-spacing-unit-factor-1-5;

                @include inuit-font-size(18px);
                color: $color--link;
                line-height: 1;
                @include animatedUnderline;
            }
        }

        &__item--level1:first-child {
            .ruag-c-navigation__submenu--level2 {
                @include flexbox;
                @include flex-wrap(wrap);
                @include justify-content(space-between);
            }

            .ruag-c-navigation__item--level2 {
                width: 28%;
            }

            .ruag-c-navigation__link--level2 {
                display:block;
                hyphens: manual;

                .ruag-c-navigation__item-title--level2, .ruag-c-navigation__item-teaser-text {
                    transition: color $global-transition-duration $global-transition-timing;
                }

                &:hover {
                    .ruag-c-navigation__item-title--level2 {
                        color: $color--primary1;
                        
                        &::before {
                            width: 100%;
                            background-color: $color--primary1;
                        }
                    }

                    .ruag-c-navigation__item-teaser-text {
                        color: $color--black;
                    }

                    .ruag-c-navigation__image--level2 {
                        filter: brightness(1.1);
                    }
                }
            }

            .ruag-c-navigation__image--level2 {
                margin-bottom: $inuit-global-spacing-unit-factor-0-5;
                filter: brightness(1);
                transition: filter $global-transition-duration $global-transition-timing;
            }

            .ruag-c-navigation__item-title--level2 {
                margin-bottom: $inuit-global-spacing-unit-factor-1;
                @include inuit-font-size(20px);
                font-weight: 400;
                @include animatedUnderline;
            }
        }
    }
}


// Drupal Admin Navigation
body.toolbar-fixed {
    .ruag-c-navigation__button-desktop,
    .ruag-c-navigation__content {
        top: 39PX;
    }

    &.toolbar-tray-open {
        &.toolbar-horizontal {
            .ruag-c-navigation__button-desktop,
            .ruag-c-navigation__content {
                top: 79PX;
            }
        }
    }
}
