@mixin contentlistPlacement($cols) {

    @if $cols == 1 {

        .ruag-c-contentlist__list {
            @include grid-columns(0, 1fr);
            grid-auto-rows: auto auto 4rem;
        }

        $a: 0;

        @for $i from 1 through 24 {
            $cols: 1;

            $ModuloWithoutZero: 0;

            @if ($i % $cols) == 0 { $ModuloWithoutZero: $cols; } @else { $ModuloWithoutZero: $i % $cols }

            .ruag-c-contentlist__item#{$i} {
                .ruag-c-card__figure,
                .ruag-c-card__number {
                    grid-row-start:    $i + $a;
                    grid-row-end:      $i + $a + 1;
                    grid-column-start: $ModuloWithoutZero;
                    grid-column-end:   $ModuloWithoutZero + 1;
                }

                .ruag-c-card__content {
                    grid-row-start:    $i + $a + 1;
                    grid-row-end:      $i + $a + 2;
                    grid-column-start: $ModuloWithoutZero;
                    grid-column-end:   $ModuloWithoutZero + 1;
                }
            }

            $a: $a + 2;
        }

    } @else if $cols == 2 {

        .ruag-c-contentlist__list {
            @include grid-columns(2rem, 1fr 1fr);
            grid-auto-rows: auto auto 8rem;
        }

        $a: 0;
        $b: -1;

        @for $i from 1 through 24 {
            $cols: 2;

            $ModuloWithoutZero: 0;

            @if ($i % $cols) == 0 { $ModuloWithoutZero: $cols; } @else { $ModuloWithoutZero: $i % $cols; }

            @if $i % 2 == 1 {
                .ruag-c-contentlist__item#{$i} {
                    .ruag-c-card__figure,
                    .ruag-c-card__number {
                        grid-row-start:    $i + $a;
                        grid-row-end:      $i + $a + 1;
                        grid-column-start: $ModuloWithoutZero;
                        grid-column-end:   $ModuloWithoutZero + 1;
                    }

                    .ruag-c-card__content {
                        grid-row-start:    $i + $a + 1;
                        grid-row-end:      $i + $a + 2;
                        grid-column-start: $ModuloWithoutZero;
                        grid-column-end:   $ModuloWithoutZero + 1;
                    }
                }

                $a: $a + 1;
            } @else {
                .ruag-c-contentlist__item#{$i} {
                    .ruag-c-card__figure,
                    .ruag-c-card__number {
                        grid-row-start:    $i + $b;
                        grid-row-end:      $i + $b + 1;
                        grid-column-start: $ModuloWithoutZero;
                        grid-column-end:   $ModuloWithoutZero + 1;
                    }

                    .ruag-c-card__content {
                        grid-row-start:    $i + $b + 1;
                        grid-row-end:      $i + $b + 2;
                        grid-column-start: $ModuloWithoutZero;
                        grid-column-end:   $ModuloWithoutZero + 1;
                    }
                }

                $b: $b + 1;
            }
        }

    } @else if $cols == 3 {

        .ruag-c-contentlist__list {
            @include grid-columns(4rem, 1fr 1fr 1fr);
        }

        @for $i from 1 through 24 {
            $cols: 3;

            $ModuloWithoutZero: 0;

            @if ($i % $cols) == 0 { $ModuloWithoutZero: $cols; } @else { $ModuloWithoutZero: $i % $cols }

            .ruag-c-contentlist__item#{$i} {
                .ruag-c-card__figure,
                .ruag-c-card__number {
                    grid-row-start:    $i - ($ModuloWithoutZero) + 1;
                    grid-row-end:      $i - ($ModuloWithoutZero) + 2;
                    grid-column-start: $ModuloWithoutZero;
                    grid-column-end:   $ModuloWithoutZero + 1;
                }

                .ruag-c-card__content {
                    grid-row-start:    $i - ($ModuloWithoutZero) + 2;
                    grid-row-end:      $i - ($ModuloWithoutZero) + 3;
                    grid-column-start: $ModuloWithoutZero;
                    grid-column-end:   $ModuloWithoutZero + 1;
                }
            }
        }

    } @else {

        @error 'Number of columns can be 1 or 2 or 3';
    }
}
