.ruag-c-button {

    position: relative;
    display: inline-block;
    padding: 8px 32px;
    overflow: hidden;

    @include inuit-font-size(18px);
    text-decoration: none;
    outline: none;

    span {
        position: relative;
        z-index: 10;
    }

    &::after {
        position: absolute;
        top: 0;
        bottom: 0;
        left: -200%;
        display: block;
        width: 300%;
        height: 100%;
        transition: left 0.3s ease-in;
        content: '';
    }


    &--primary1 {

        &:link, &:visited, &:hover, &:active, &:focus {
            color: $color--white;
        }
        

        &::after {
            background-image: linear-gradient(-45deg, $color--monza-red 50%, $color--red 50%);
        }

        &.ruag-c-button--inverse::after {
            background-image: linear-gradient(-45deg, $color--japanesemaple-red 50%, $color--rosewood-red 50%);
        }
    }

    &--primary1:hover {

        &::after {
            left: 0;
        }
    }


    &--primary2 {

        &:link, &:visited {
            color: $color--black;
        }

        &:hover, &:active, &:focus {
            color: $color--black;
        }
        
        &::after {
            background-image: linear-gradient(-45deg, $color--pelorous-green 50%, $color--riptide-green 50%);
        }

        &.ruag-c-button--inverse::after {
            background-image: linear-gradient(-45deg, $color--pelorous-green 50%, $color--jellybean-green 50%);
        }
    }

    &--primary2:hover {

        &::after {
            left: 0;
        }
    }


    &--secondary {
        padding: 7px 31px;

        &:link, &:visited {
            color: $color--monza-red;
        }

        &:hover, &:active, &:focus {
            color: $color--japanesemaple-red;
            border-color: $color--japanesemaple-red;
        }

        border: 1PX solid $color--monza-red;

        &.ruag-c-button--inverse {

            &:link, &:visited {
                color: $color--white;
                border-color: $color--white;
            }

            &:hover, &:active, &:focus {
                color: rgba($color--white, 0.8);
                border-color: rgba($color--white, 0.8);
            }
            
        }
    }
}


.ruag-o-section--bg-dark {
    .ruag-c-button {
        &--primary2 {
            &:link, &:visited, &:hover, &:active, &:focus {
                color: $color--black;
            }
        }

        &--secondary {

            &:link, &:visited {
                color: $color--text-inverse;
                border-color: $color--text-inverse;
            }
            
            &:hover, &:active, &:focus {
                color: $color--primary1;
                border-color: $color--primary1;
            }
        }
    }
}

