@mixin linkArrow($direction: 'right') {
    display: inline-block;
    margin-left: $inuit-global-spacing-unit-factor-0-5;
    color: $color--primary1;
    transform: translateX(0);
    transition: transform $global-transition-duration;

    @if $direction == 'left' {
        content: '\2190';
    }

    @if $direction == 'up' {
        content: '\2191';
    }

    @if $direction == 'right' {
        content: '\2192';
    }

    @if $direction == 'down' {
        content: '\2193';
    }

}


@mixin linkArrow--hover {
    transform: translateX(5px);
}
