.ruag-c-wysiwyg {
    $list-margin-left: 1.8rem;
    $list-margin-ul: $inuit-global-spacing-unit-factor-3;
    $list-margin-li: $inuit-global-spacing-unit-factor-1;

    * {
        text-align: left !important;
    }

    p {
        margin-bottom: 2rem;
    }

    a {
        font-weight: normal;

        &:link,
        &:visited {
            color: $color--link;
        }
        
        &:hover,
        &:active,
        &:focus {
            color: $color--link-hover;
        }
    }


    ul,
    ol {
        margin: 0 0 $list-margin-ul $list-margin-left;
        padding: 0;
        
        ul, ol {
            margin-top: $list-margin-li;
            margin-bottom: $list-margin-li;
        }
        
        li {
            margin-bottom: $list-margin-li;
        }
    }

    /* unordered list */
    ul {
        
        li {
            position: relative;
            list-style: none;

            &::before {
                position: absolute;
                left: -$list-margin-left;
                color: $color--regent-gray;
                content: '\2014';
            }

            ul > li::before {
                content: '\2013';
            }
        }
    }

    /* ordered list */
    ol {
        margin-left: 16px;
        list-style-type: decimal;

        ::marker {
            color: $color--regent-gray;

        }

        li {
            padding-left: 12px;
        }

        > li > ol {
            list-style-type: lower-latin;

            > li > ol {
                list-style-type: lower-roman;
            }
        }
    }
}


.ruag-o-section--bg-dark {
    .ruag-c-wysiwyg {
        a {

            &:link,
            &:visited {
                color: $color--link-inverse;
            }
            
            &:hover,
            &:active,
            &:focus {
                color: $color--link-hover-inverse;
            }
        }
    }
}
