.ruag-c-jumbotron {
    &--input {
        height: 28vh;
        color: $color--white;
        background-color: $color--monza-red;
        
        & ~ .ruag-c-title {
            @extend .ruag-o-section--has-truncated-predecessor;

            &::before {
                border-right-color: $color--white;
                border-bottom-color: $color--white;
            }
            padding:0;
        }

        .ruag-c-jumbotron__inner {
            @include display-grid;
            @include grid-columns(20px, repeat(12, 1fr));
            //@include flexbox;
            //@include flex-wrap(wrap);
            @include align-content(center);
        }

        .ruag-c-jumbotron__text {
            grid-column-start: 4;
            grid-column-end: 10;
            margin-bottom:$inuit-global-spacing-unit-factor-2;
        }

        .ruag-c-jumbotron__form {
            grid-column-start: 4;
            grid-column-end: 10;
            width:100%;
        }
    }
}

.ruag-search-results {
    &__item {
        margin-bottom: $inuit-global-spacing-unit-factor-4;
        @include display-grid;
        @include grid-columns(20px, repeat(12, 1fr));
    }

    &__indicator {
        margin-bottom: $inuit-global-spacing-unit-factor-3;
        text-transform: uppercase;
        @include display-grid;
        @include grid-columns(20px, repeat(12, 1fr));
    }
    
    &__indicator-results {
        grid-column-start: 4;
        grid-column-end: 10;
    }
}


.ruag-search-result {
    grid-column-start: 3;
    grid-column-end: 10;
    @include display-grid;
    @include grid-columns(20px, repeat(7, 1fr));
    grid-column-gap: 20px;
    text-decoration:none;
    
    &:hover {
        .ruag-o-title__inner {
            color: $color--primary1;

            &::before {
                width: 100%;
                background-color: $color--primary1;
            }
        }
    }

    &__image {
        grid-column-start: 1;
        grid-column-end: 2;

        img {
            width:100%;
        }
    }

    &__content {
        grid-column-start: 2;
        grid-column-end: 8;
    }
}

.list {
    &--unstyled {
        margin: 0;
        padding: 0;
        list-style: none;
    }
}
