.ruag-c-contentlist {

    // general
    &__item {
        margin-bottom: $inuit-global-spacing-unit-factor-3;
    }


    @include mq($from: tablet) {
        .ruag-c-card__figure {
            width: 84%;
        }
    }


    // IE11 Grid
    .ruag-c-card {
        display: block;
    }

    &__list {
        // IE11 Grid
        display: -ms-grid;
        -ms-grid-rows: 1fr $inuit-global-spacing-unit-factor-4 1fr $inuit-global-spacing-unit-factor-4 1fr;

        //general
        margin: 0;
        padding: 0;
    }

    &__item1 {
        -ms-grid-row: 1;
    }

    &__item2 {
        -ms-grid-row: 3;
    }

    &__item3 {
        -ms-grid-row: 5;
    }


    @include mq($from: tablet) {
        &__list {
            -ms-grid-columns: 1fr $inuit-global-spacing-unit-factor-4 1fr $inuit-global-spacing-unit-factor-4 1fr;
            -ms-grid-rows: 1fr;
        }

        &__item1 {
            -ms-grid-row: 1;
            -ms-grid-column: 1;
        }

        &__item2 {
            -ms-grid-row: 1;
            -ms-grid-column: 3;
        }

        &__item3 {
            -ms-grid-row: 1;
            -ms-grid-column: 5;
        }
    }

}



// Others Grid
@supports (grid-template-areas: 'ruag') {

    .ruag-c-contentlist {
        .ruag-c-card {
            display: contents;
        }


        &__list {
            @include display-grid();
        }

        @include contentlistPlacement(1);


        @include mq($from: phablet) {
            @include contentlistPlacement(2);
        }


        @include mq($from: tablet) {
            @include contentlistPlacement(3);
        }
    }

}
