.ruag-c-slideshow {
    @include joinimation();

    @include flexbox;
    @include flex-wrap(wrap);
    @include justify-content(center);

    .o-figure {
        width: 100%;

        &__inner {
            margin-bottom: $inuit-global-spacing-unit-factor-0-75;
        }

        &__caption {
            margin-top: 0;
        }
    }

    picture {
        display: block;
    }
}


.flickity {
    &-viewport {
        order: 1;
        width: 100%;
        margin-bottom: $inuit-global-spacing-unit-factor-1;
    }

    &-button {
        position: static;
        z-index: 20;
        padding: 10PX;
        background: none;

        &-icon {
            width: 24PX;
            height: 24PX;
        }

        path {
            transition: fill $global-transition-duration $global-transition-timing;
        }

        &:hover {
            background: none;

            @media (any-hover: hover) {
                path {
                    fill: $color--primary1;
                }
            }
        }

        &:disabled {
            opacity: 0.25;
        }
    }

    &-prev-next-button {
        bottom: -25PX;
        transform: translateY(-15PX);
        transition: transform $global-transition-duration;

        &.previous {
            order: 2;
            margin-right: $inuit-global-spacing-unit-factor-1;

            &:hover {

                @media (any-hover: hover) {
                    transform: translate(-5px, -15PX);
                }

            }
        }

        &.next {
            order: 4;
            margin-left: $inuit-global-spacing-unit-factor-1;

            &:hover {

                @media (any-hover: hover) {
                    transform: translate(5px, -15PX);
                }

            }
        }
    }

    &-page-dots {
        position: static;
        order: 3;
        width: auto;
    }
}

.ruag-o-section--bg-default,
.ruag-o-section--bg-bright {
    .flickity {
        &-button {
            color: $color--text;
        }

        &-page-dots .dot {
            background: $color--text;
            transition: background $global-transition-duration $global-transition-timing, opacity $global-transition-duration $global-transition-timing;

            &:not(.is-selected):hover {
                background: $color--primary1;
                opacity: 1;
            }
        }
    }
}

/* stylelint-disable */
.ruag-o-section--bg-dark {
    .flickity {
        &-button {
            color: $color--white-off1;
        }

        &-page-dots .dot {
            background: $color--white-off1;
            transition: background $global-transition-duration $global-transition-timing, opacity $global-transition-duration $global-transition-timing;

            &:not(.is-selected):hover {
                background: $color--primary1;
                opacity: 1;
            }
        }
    }
}
/* stylelint-enable */
