.ruag-o-title {
    margin-bottom: $inuit-global-spacing-unit-factor-1-5;

    &__inner {
        margin-bottom: 0;
    }

    &--has-underline {
        margin-bottom: $inuit-global-spacing-unit-factor-1;


        .ruag-o-title__inner {
            @include animatedUnderline;
        }
    }

    &--has-tag {
        .ruag-c-card__title-inner {
            position: relative;
        }

        .ruag-o-title__inner {
            padding-right: 30PX;
        }
    }

    &--page-title {
        padding: $inuit-global-spacing-unit-factor-2 0 0;

        @include mq($from: tablet) {
            padding: $inuit-global-spacing-unit-factor-8 0 0;
        }

        p {
            margin: 0;
        }
    }

}
