@mixin animatedUnderline() {
    position: relative;
    display: inline-block;
    padding-bottom: $inuit-global-spacing-unit-factor-0-75;

    &::before {
        position: absolute;
        bottom: 0;
        z-index: 10;
        display: block;
        width: 1.875rem;
        height: 2PX;
        background-color: $color--gull-gray;
        content: '';
        @include transition(
                background-color $global-transition-duration $global-transition-timing,
                width $global-transition-duration $global-transition-timing);
    }

    &:hover, &:active, &:focus {
        color: $color--primary1;


        &::before {
            width: 100%;
            background-color: $color--primary1;
        }
    }
}
