.ruag-o-select {
    position: relative;
    width: 100%;
    margin-bottom: $inuit-global-spacing-unit-factor-2;

    &::after {
        position: absolute;
        top: 50%;
        right: 1rem;
        width: 0;
        height: 0;
        border: 0.4rem solid transparent;
        border-top-color: $color--palesky-gray;
        transform: translateY(-25%);
        transition: border-top-color $global-transition-duration $global-transition-timing;
        content: '';
        pointer-events: none;
    }

    &__element {
        width: 100%;
        padding: 0.7rem 2rem 0.7rem 1rem;
        color: $color--text;
        background-color: transparent;
        border: 1px solid $color--loblolly-gray;
        border-radius: 0;
        outline: none;
        cursor: pointer;
        transition: $global-transition-duration $global-transition-timing;
        transition-property: color, border-color;
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;

        &::-ms-expand {
            display: none;
        }

        &:focus::-ms-value {
            background-color: transparent;
        }
    }

    &:hover {
        &::after {
            border-top-color: $color--secondary;
        }

        .ruag-o-select__element {
            color: $color--secondary;
            border-color: $color--secondary;

            &:focus {
                color: $color--text;
            }
        }
    }

    &--inverse {
        &::after {
            border-color: $color--text-inverse transparent transparent transparent;
        }

        .ruag-o-select__element {
            color: $color--text-inverse;
        }
    }
}



.ruag-o-section--bg-dark {
    .ruag-o-select {
        &::after {
            border-color: $color--text-inverse transparent transparent transparent;
        }


        &__element {
            color: $color--text-inverse;
        }
    }
}
