.ruag-o-section {
    padding: 6rem 0;

    @include mq($from: tablet) {
        padding: $inuit-global-spacing-unit-factor-8 0;
    }


    &__inner {
        max-width: $global-section-max-width;
        margin: 0 auto;
        padding: 0 $global-section-side-padding;
    }


    &--has-truncated-predecessor {
        position: relative;

        &::before {
            position: absolute;
            top: calc(((0.066 * 100vw) + 70px) * -1);  // 10px less, so that there is no line below the triangle
            right: 0;
            z-index: 20;
            width: 0;
            height: 0;
            background-color: transparent;
            border: calc(((0.066 * 100vw) + 80px) / 2) solid transparent;
            // function for the triangle size:
            // 0.066 * 100vw + 75 (y = ax + b)

            content: '';
        }
    }


    &--no-padding {
        padding: 0;
    }


    &--small-padding {
        padding: 2rem;
    }


    &--bg-primary {
        background-color: $color--monza-red;

        &::before,
        &::after {
            border-right-color:  $color--monza-red;
            border-bottom-color: $color--monza-red;
        }
    }


    &--bg-default {
        background-color: $color--white;

        &::before,
        &::after {
            border-right-color:  $color--white;
            border-bottom-color: $color--white;
        }
    }


    &--bg-bright {
        background-color: $color--porcelain-gray;

        &::before,
        &::after {
            border-right-color:  $color--porcelain-gray;
            border-bottom-color: $color--porcelain-gray;
        }
    }


    &--bg-dark {
        color: $color--text-inverse;
        background-color: $color--abbey-gray;

        &::before,
        &::after {
            border-right-color:  $color--abbey-gray;
            border-bottom-color: $color--abbey-gray;
        }
    }
}
