/**
 * Text alignment
 */

.u-text-left { text-align: left  !important; }

.u-text-center { text-align: center!important; }

.u-text-right { text-align: right !important; }


/**
 * Font weights
 */

.u-weight-ultralight { font-weight: 100 !important; }

.u-weight-light { font-weight: 300 !important; }

.u-weight-normal { font-weight: 400 !important; }

.u-weight-semibold { font-weight: 500 !important; }

.u-weight-bold { font-weight: 700 !important; }


/**
 * Font sizes
 */

.u-size-tiny { @include inuit-font-size(12px, $important: true); }

.u-size-small { @include inuit-font-size(14px, $important: true); }

.u-size-large { @include inuit-font-size(20px, $important: true); }

.u-size-huge { @include inuit-font-size(24px, $important: true); }


/**
 * Text wrapping
 */

.u-nowrap { white-space: nowrap }
