// General
.ruag-c-jobteaser {

    &__inner {
        display: flow-root;
        margin: - ($inuit-global-spacing-unit-factor-2 / 2);
        overflow: auto;
    }


    &__item {
        float: left;
        width: 25%;
        min-width: 250px;
        height: 250px;
        padding: $inuit-global-spacing-unit-factor-2 / 2;
        text-decoration: none;


        &-inner {
            @include truncate($color--porcelain-gray);
            height: 100%;
            padding: $inuit-global-spacing-unit-factor-1-5;
        }


        &:hover {
            .ruag-c-jobteaser__title {
                color: $color--primary1;

                &::before {
                    width: 100%;
                    background-color: $color--monza-red;
                }
            }

            .ruag-c-jobteaser__title::after {
                //right: - 0.5rem;
                left: 8px;
            }
        }


        &--accent {
            .ruag-c-jobteaser__item-inner {
                color: $color--text-inverse;
                @include truncate($color--monza-red);
            }

            .ruag-c-jobteaser__content {
                margin-bottom: $inuit-global-spacing-unit-factor-1-5;
            }

            .ruag-c-jobteaser__title::before {
                background-color: $color--text-inverse;
            }

            &:hover .ruag-c-jobteaser__title {
                color: $color--text-inverse;

                &::before {
                    background-color: $color--text-inverse;
                }
            }
        }
    }


    &__title {
        @include animatedUnderline();
        transition: color $global-transition-duration $global-transition-timing;

        //span {
        //    display: -webkit-box;
        //    width: calc(100% - 1.5rem);
        //    -webkit-line-clamp: 2;
        //    -webkit-box-orient: vertical;
        //    overflow: hidden;
        //}


        &::after {
            //position: absolute;
            //right: 0;
            //bottom: 0.75rem;
            //transition: right $global-transition-duration $global-transition-timing;
            //content: ' →';
            position: relative;
            left: 0;
            transition: left $global-transition-duration $global-transition-timing;
            content: ' →';
        }
    }


    &__gender,
    &__percentage {
        display: inline-block;
    }


    &__gender {
        width: 43px;
    }


    &__location {
        display: block;
    }

}




// IE11 Grid
.c-jobteaser {

    &__inner {
        display: flow-root;
        margin: - ($inuit-global-spacing-unit-factor-2 / 2);
        overflow: auto;
    }


    &__item {
        float: left;
        width: 25%;
        min-width: 250px;
        height: 250px;
        padding: $inuit-global-spacing-unit-factor-2 / 2;


        &-inner {
            height: 100%;
        }
    }

}



// Others Grid

@supports (display: grid) {
    .ruag-c-jobteaser {
        &__inner {
            display: grid;
            grid-row-gap: $inuit-global-spacing-unit-factor-2;
            grid-column-gap: $inuit-global-spacing-unit-factor-2;
            grid-template-columns: repeat(auto-fit, minmax(14rem, 1fr));
            margin: unset;
        }


        &__item {
            float: none;
            width: unset;
            min-width: unset;
            height: unset;
            padding: unset;
        }
    }
}
