/*
## Usage: ##
Always define the grid with [ display-grid() ], [ grid-columns() ] and [ grid-rows() ].
Then continue with either [ grid-autoflow() ] or [ grid-matrix() ] and [ grid-area() ].

    * example with [ grid-autoflow() ]: *
    @include display-grid();
    @include grid-columns(1.5em, 1fr 1fr 1fr);
    @include grid-rows(3em, auto auto);
    @include grid-autoflow((autoflow: column, columns: 3, rows: 2, gap: both));

    * example with [ grid-matrix() ] and [ grid-area() ]: *
    - (on the grid-container) -
    @include display-grid();
    @include grid-columns(1.5em, 1fr 1fr 1fr 1fr);
    @include grid-rows(3em, auto auto);
    $gridAreaMap: (
        row1: ('logo', 'nav', 'nav', 'nav'),
        row2: ('logo', '.', 'search', 'search')
    );
    @include grid-matrix($gridAreaMap);

    - (on the grid-items) -
    @include grid-area(logo, $gridAreaMap, true);


## API ##
[ display-grid ]

[ grid-columns($gap, $columns) ]
$gap     {string} Optional column gap
$columns {list}   Columns sizes

    * example: *
    @include grid-columns(10px, 1fr 3fr 2fr);


[ grid-rows($gap, $rows) ]
$gap  {string} Optional row gap
$rows {list}   Rows sizes

    * example: *
    @include grid-rows(10px, auto auto auto);


[ grid-autoflow($data) ]
$data {map}
$data.autoflow {string} Value or "grid-auto-flow". Either 'column' or 'row'.
$data.columns  {number} Number of columns in the grid.
$data.rows     {number} Numbers of rows in the grid.
$data.gap      {string} Where to allow for gaps. Either 'column', 'row' or 'both'.

    * example: *
    @include grid-autoflow((autoflow: column, columns: 3, rows: 2, gap: both));


[ grid-matrix($map) ]
$map {map} Map of grid area names.

    * example: (on the grid container) *
    $gridAreaMap: (
        row1: ('logo', 'nav', 'nav', 'nav'),
        row2: ('logo', '.', 'search', 'search')
    );
    @include grid-matrix($gridAreaMap);


[ grid-area($area, $map, $columnGap: false, $rowGap: false) ]
$area      {string} Name of the area.
$map       {map}    Map of grid area names.
$columnGap {bool}   Whether to use gaps for the columns.
$rowGap    {bool}   Whether to use gaps for the rows.

    * example: (on the grid items) *
    @include grid-area(logo, $gridAreaMap, true);


## Ressources ##
- gridbyexample.com (Rachel Andrews)                                               [https://gridbyexample.com/]
- Should I try to use the IE implementation of CSS Grid Layout? (Rachel Andrews)   [https://rachelandrew.co.uk/archives/2016/11/26/should-i-try-to-use-the-ie-implementation-of-css-grid-layout/]
- MDN                                                                              [https://developer.mozilla.org/en-US/docs/Learn/CSS/CSS_layout/Grids]
- CSS-Tricks                                                                       [https://css-tricks.com/snippets/css/complete-guide-grid/]
- Mortan Rand-Hendriksen on Smashing Magazine and Youtube                          [https://www.smashingmagazine.com/2017/06/building-production-ready-css-grid-layout/]  [https://www.youtube.com/watch?v=7kVeCqQCxlk]
- Post for the SCSS Grid Mixins                                                    [https://medium.com/@gisugosu/css-grid-layout-spec-2-to-spec-1-transpiler-with-sass-415dff4dd31b]
 */

@import "../../node_modules/sassy-lists/dist/SassyLists";

@mixin display-grid {
    display: -ms-grid;
    display: grid;
}

/// Add Gap between the boxes
///
/// @author Sascha Fuchs
///
/// @group core - cssgrid
///
/// @param   {list}    $boxes - List with box sizes
/// @param   {string}  $gap - Optional column gap

@function box-gap($boxes, $gap) {
    $box: ();

    @for $i from 1 through length($boxes) {
        $box: append($box, nth($boxes, $i), space);

        // Adding Gap Between

        @if $gap > 0 {
            // Not last Loop

            @if $i != length($boxes) {
                $box: append($box, $gap, space);
            }
        }
    }

    @return $box;
}

/// To build Grid Template Columns with optional gap
///
/// @author Sascha Fuchs
///
/// @group core - cssgrid
///
/// @param   {string}  $gap - Optional column gap
/// @param   {list}    $columns - Columns sizes
///
/// @require {function} box-gap
///
/// @example scss - scss
///   .test {
///     @include grid-columns(10px 250px 1fr 50px 100px);
///   }

@mixin grid-columns($gap, $columns) {
    grid-template-columns: $columns;

    @if $gap > 0 {
        grid-column-gap: $gap;
    }

    -ms-grid-columns: box-gap($columns, $gap);
}

/// To build Grid Template Rows with optional gap
///
/// @author Sascha Fuchs
///
/// @group core - cssgrid
///
/// @param   {string}  $gap - Optional row gap
/// @param   {list}    $rows - Rows sizes
///
/// @require {function} box-gap
///
/// @example scss - scss
///   .test {
///     @include grid-rows(10px 1fr);
///   }

@mixin grid-rows($gap, $rows) {
    grid-template-rows: $rows;

    @if $gap > 0 {
        grid-row-gap: $gap;
    }

    -ms-grid-rows: box-gap($rows, $gap);
}

/// Generates IE10/11 grid placement to simulate grid-auto-flow.
///
/// @author Katherine Senzee
///
/// @param {map}    $data - Information needed to tell IE exactly where to place everything.
/// @param {string} $data.autoflow - value for "grid-auto-flow". Either 'column' or 'row'.
/// @param {number} $data.columns - number of columns in the grid
/// @param {number} $data.rows - number of rows in the grid
/// @param {string} $data.gap - Where to allow for gaps. Should be 'column', 'row', or 'both'.
///
/// @example scss -
///   .quotes {
///     @include grid-autoflow(autoflow: column, columns: 3, rows: 6, gap: both);
///   }

@mixin grid-autoflow($data) {
    $autoflow: map-get($data, autoflow);
    $columns: map-get($data, columns);
    $rows: map-get($data, rows);

    $columnGap: false;
    $rowGap: false;

    @if (map-get($data, 'gap') == 'column') {
        $columnGap: true;
        $rowGap: false;
    } @else if (map-get($data, gap) == 'row') {
        $columnGap: false;
        $rowGap: true;
    } @else if (map-get($data, gap) == 'both') {
        $columnGap: true;
        $rowGap: true;
    } @else if (map-get($data, gap) == 'neither') {
        $columnGap: false;
        $rowGap: false;
    } @else {

        @error The 'gap' argument must be one of 'column', 'row', 'both', or 'neither';
    }

    @if ($columnGap) {
        $columns: $columns * 2 - 1;
    }

    @if ($rowGap) {
        $rows: $rows * 2 - 1;
    }

    grid-auto-flow: $autoflow;
    $column: 1;
    $row: 1;
    $counter: 1;

    @if ($autoflow == 'column') {

        @for $i from 1 through $columns {

            @for $j from 1 through $rows {
                // This element can be filled iff we are not in a row gap or a column gap.

                @if ($i % 2 == 0 and $columnGap) {
                    // This is a column gap. Don't place the next element.
                } @else if ($j % 2 == 0 and $rowGap) {
                    // This is a row gap. Don't place the next element.
                } @else {
                    // Place the next element in column $i and row $j.
                    :nth-child(#{$counter}) {
                        -ms-grid-row: $j;
                        -ms-grid-column: $i;
                    }
                    $counter: $counter + 1;
                }
            }
        }
    } @else if ($autoflow == 'row') {

        @for $i from 1 through $rows {

            @for $j from 1 through $columns {
                // This element can be filled iff we are not in a row gap or a column gap.

                @if ($i % 2 == 0 and $rowGap) {
                    // This is a row gap. Don't place the next element.
                } @else if ($j % 2 == 0 and $columnGap) {
                    // This is a column gap. Don't place the next element.
                } @else {
                    // Place the next element in row $i and column $j.
                    :nth-child(#{$counter}) {
                        -ms-grid-row: $i;
                        -ms-grid-column: $j;
                    }
                    $counter: $counter + 1;
                }
            }
        }
    }

}





/// Generate a Grid Template Area from a Grid Map
///
/// @group core - cssgrid
///
/// @param   {string}  $area - Name of the Area
/// @param   {map}     $map - Map of your grid area matrix
///
/// @example scss - scss
///    $gridAreaMap: (
///      row1: ('logo', 'nav', 'nav', 'nav'),
///      row2: ('logo', 'empty', 'search', 'search')
///    );
///    @include grid-matrix($gridAreaMap);
@mixin grid-matrix($map) {
    $matrix: ();

    @each $index, $row in $map {
        $list: null;

        @for $i from 1 through length($row) {
            $list: append($list, unquote(nth(($row), $i)), space);
        }

        $matrix: append($matrix, quote($list), space);
    }

    grid-template-areas: $matrix;
}





/// Generate Coordinates based on Grid Matrix and the selected area
///
/// @group core - cssgrid
///
/// @param   {string}  $area - Name of the Area
/// @param   {map}     $map - Map of your grid area matrix
/// @param   {bool}    $columnGap - If you use Gap for Columns
/// @param   {bool}    $rowGap - If you use Gap for Rows
///
/// @example scss - scss
///    $gridAreaMap: (
///      row1: ('logo', 'nav', 'nav', 'nav'),
///      row2: ('logo', 'empty', 'search', 'search')
///    );
///    @include grid-area(logo, $gridAreaMap, true);
@mixin grid-area($area, $map, $columnGap: false, $rowGap: false) {
    // Init Vars
    $gridColumnStart: '';
    $gridColumnEnd: '';
    $gridRows: ();
    $gridRowHeight: '';
    $areaExists: 0;

    // Output regular Area
    grid-area: $area;

    // Generate Old Spec Coordinates based on Area
    // Get Width element
    @for $i from 1 through length($map) {

        @if sl-contain(map-get($map,row#{$i}), $area) {
            $areaExists: append($areaExists, 1);
            $gridRowHeight: append($gridRowHeight, $i);
            $gridColumnStart: index(map-get($map,row#{$i}), $area);
            $gridColumnEnd: sl-last-index(map-get($map,row#{$i}), $area);
            $gridRows: append($gridRows, $i);
        }
    }

    // If Area exists in the Map
    @if length($areaExists) > 1 {
        // Convert Coordinate if Column Gap is active
        @if $columnGap {
            -ms-grid-column: $gridColumnStart + ($gridColumnStart - 1);
            -ms-grid-column-span: $gridColumnEnd - $gridColumnStart + 1 + ($gridColumnEnd - $gridColumnStart);

        } @else {
            -ms-grid-column: $gridColumnStart;
            -ms-grid-column-span: $gridColumnEnd - $gridColumnStart + 1;
        }

        // Add Row Coordinates
        @if $rowGap {
            -ms-grid-row: nth($gridRows,1) + (nth($gridRows,1) - 1);
            -ms-grid-row-span: length($gridRowHeight) - 1 + (length($gridRowHeight) - 2);

        } @else {
            -ms-grid-row: nth($gridRows,1);
            -ms-grid-row-span: length($gridRowHeight) - 1;
        }
    }
}

