.ruag-o-link {
    &__inner {
        text-decoration: none;
    }

    &--has-icon {
        position: relative;
        margin-left: $inuit-global-spacing-unit-factor-3;

        svg {
            position: absolute;
            top: -7PX;
            left: -$inuit-global-spacing-unit-factor-3;
            height: 2rem;

            @include mq($from: tablet) {
                top: -4PX;
            }

            path {
                fill: $color--primary1;
            }
        }
    }

    &--animated {
        .ruag-o-link__inner {
            @include mq($from: tablet) {
                @include animatedUnderline;
            }
        }
    }


    &--big {
        @include inuit-font-size(28px);
    }

}
