.ruag-c-quote {
    @include joinimation();

    position: relative;
    color: $color--white-off1;

    &__inner {
        .ruag-o-figure {
            transform: translateZ(0);
            filter: brightness(1);
            transition: filter $global-transition-duration $global-transition-timing;
        }
    }

    &__link {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 30;
        display: block;

        & ~ .ruag-c-quote__inner .ruag-c-quote__quote::after {
            display: inline-block;
            margin-left: $inuit-global-spacing-unit-factor-1;
            color: $color--primary1;
            transform: translateX(0);
            transition: transform $global-transition-duration;
            content: '\2192';
        }
    }

    &__content {
        padding: $inuit-global-spacing-unit-factor-1-5 $inuit-global-spacing-unit-factor-3 $inuit-global-spacing-unit-factor-3 $inuit-global-spacing-unit-factor-1-5;
        @include truncate($color--abbey-gray);
        transition: background-color $global-transition-duration $global-transition-timing;

        @include mq($from: phablet) {
            padding:
                $inuit-global-spacing-unit-factor-3
                $inuit-global-spacing-unit-factor-8
                $inuit-global-spacing-unit-factor-3
                $inuit-global-spacing-unit-factor-3;
        }
    }

    &__link:hover {
        & ~ .ruag-c-quote__inner {
            .ruag-c-quote__content {
                @include truncate(darken($color--abbey-gray, 5%));
            }

            .ruag-o-figure {
                filter: brightness(1.1);
            }

            .ruag-c-quote__quote::after {
                transform: translateX(5PX);
            }
        }
    }

    &__quote {
        margin: 0 0 $inuit-global-spacing-unit-factor-3 0;
        @include inuit-font-size(20px);

        @include mq($from: phablet) {
            @include inuit-font-size(28px);
        }
    }

    &__author {
        @include inuit-font-size(16px);
    }
}


.ruag-o-section--bg-dark {
    .ruag-c-quote {
        &__content {
            color: $color--text;
            background-color: $color--porcelain-gray;
        }

        &__link:hover {
            & ~ .ruag-c-quote__inner {
                .ruag-o-figure {
                    filter: brightness(1.1);
                }

                .ruag-c-quote__content {
                    background-color: $color--white;
                }
            }
        }
    }
}
