.ruag-c-gallery {
    // general {
    &__image {
        &--mobile-hidden {
            @include mq($until: phablet) {
                height: 0;
                visibility: hidden;
            }
        }
    }


    // IE11
    .ruag-c-card {
        display: block;
    }

    // general
    &__row {
        display: -ms-grid;

        // horizontal margin between the grids
        &:not(:last-child) {
            margin-bottom: $inuit-global-spacing-unit-factor-2;

            @include mq($from: tablet) {
                margin-bottom: 0;
            }
        }

        &:not(:first-child) {
            margin-top: $inuit-global-spacing-unit-factor-2;
        }

        .ruag-c-card__figure {
            margin-bottom: 0;
        }

        .ruag-c-card__content {
            margin-top: $inuit-global-spacing-unit-factor-0-5;
        }


        &--3items {
            // IE11
            -ms-grid-columns: 100%;
            -ms-grid-rows: 1fr $inuit-global-spacing-unit-factor-2 1fr $inuit-global-spacing-unit-factor-2 1fr;

            .ruag-c-card:nth-child(1) {
                -ms-grid-row: 1;
            }

            .ruag-c-card:nth-child(2) {
                -ms-grid-row: 3;
            }

            .ruag-c-card:nth-child(3) {
                -ms-grid-row: 5;
            }

            // general
            // horizontal margin between the grids
            .ruag-c-card:nth-child(1) .ruag-c-card__content,
            .ruag-c-card:nth-child(3) .ruag-c-card__content {
                @include mq($from: tablet) {
                    margin-bottom: $inuit-global-spacing-unit-factor-2;
                }
            }
        }

        &--2items {
            -ms-grid-rows: 1fr $inuit-global-spacing-unit-factor-2 1fr;

            .ruag-c-card:nth-child(1) {
                -ms-grid-row: 1;
            }

            .ruag-c-card:nth-child(2) {
                -ms-grid-row: 3;
            }
        }


        @include mq($from: phablet) {
            &--3items {
                -ms-grid-columns: 1fr $inuit-global-spacing-unit-factor-2 1fr;
                -ms-grid-rows: 1fr $inuit-global-spacing-unit-factor-2 1fr;

                .ruag-c-gallery__item1 {
                    -ms-grid-row: 1;
                    -ms-grid-column: 1;
                    -ms-grid-row-span: 3;
                }

                .ruag-c-gallery__item2 {
                    -ms-grid-row: 3;
                    -ms-grid-column: 1;
                }

                .ruag-c-gallery__item3 {
                    -ms-grid-row: 5;
                    -ms-grid-column: 3;
                }
            }

            &--2items {
                -ms-grid-columns: 1fr $inuit-global-spacing-unit-factor-2 1fr;

                .ruag-c-gallery__item1 {
                    -ms-grid-column: 1;
                }

                .ruag-c-gallery__item2 {
                    -ms-grid-column: 3;
                }
            }
        }


        @include mq($from: tablet) {
            &--3items {
                -ms-grid-columns: 2fr $inuit-global-spacing-unit-factor-2 1fr;
                -ms-grid-rows: 1fr $inuit-global-spacing-unit-factor-2 1fr;

                //.c-gallery__item1 {
                .ruag-c-card:nth-child(1) {
                    -ms-grid-row: 1;
                    -ms-grid-column: 1;
                    -ms-grid-row-span: 3;
                }

                //.c-gallery__item2 {
                .ruag-c-card:nth-child(2) {
                    -ms-grid-row: 1;
                    -ms-grid-column: 3;
                }

                //.c-gallery__item3 {
                .ruag-c-card:nth-child(3) {
                    -ms-grid-row: 3;
                    -ms-grid-column: 3;
                }
            }

            &--2items {
                -ms-grid-columns: 1fr $inuit-global-spacing-unit-factor-2 1fr;
                -ms-grid-rows: auto;

                .ruag-c-card:nth-child(1) {
                    -ms-grid-row: 1;
                    -ms-grid-column: 1;
                }

                .ruag-c-card:nth-child(2) {
                    -ms-grid-row: 1;
                    -ms-grid-column: 3;
                }
            }
        }
    }
}




// Others Grid
@supports (grid-template: 'peter' auto / 1fr) {
    .ruag-c-gallery__row {
        display: grid;

        .ruag-c-card {
            display: contents;
        }


        &--3items {
            grid-template:
                'image1'   auto
                'content1' auto
                '.'        $inuit-global-spacing-unit-factor-2
                'image2'   auto
                'content2' auto
                '.'        $inuit-global-spacing-unit-factor-2
                'image3'   auto
                'content3' auto
                /  100%;


            @include mq($from: phablet) {
                grid-column-gap: $inuit-global-spacing-unit-factor-2;
                grid-template:
                    'image1   image1'   auto
                    'content1 content1' auto
                    '.      .'          $inuit-global-spacing-unit-factor-2
                    'image2   image3'   auto
                    'content2 content3' auto
                    / 1fr     1fr;

            }


            @include mq($from: tablet) {
                grid-template:
                    'image1   image2'   auto
                    'image1   content2' auto
                    'image1   .'        $inuit-global-spacing-unit-factor-2
                    'image1   image3'   auto
                    'content1 content3' auto
                    / 2fr     1fr;
            }
        }


        &--2items {
            grid-template:
                'image1'   auto
                'content1' auto
                '.'        $inuit-global-spacing-unit-factor-2
                'image2'   auto
                'content2' auto
                / 1fr;


            @include mq($from: phablet) {
                grid-column-gap: $inuit-global-spacing-unit-factor-2;
                grid-template:
                    'image1   image2'   auto
                    'content1 content2' auto
                    / 1fr     1fr;

            }

        }

        // first item in block of 3
        &--3items .ruag-c-card:first-child .ruag-o-figure {
            &__inner,
            &__inner .media {
                width: 100%;
                height: 100%;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        // placing items in the grid
        .ruag-c-card:nth-child(1) {
            .ruag-c-card__figure {
                grid-area: image1;
            }

            .ruag-c-card__content {
                grid-area: content1;
            }
        }

        .ruag-c-card:nth-child(2) {
            .ruag-c-card__figure {
                grid-area: image2;
            }

            .ruag-c-card__content {
                grid-area: content2;
            }
        }

        .ruag-c-card:nth-child(3) {
            .ruag-c-card__figure {
                grid-area: image3;
            }

            .ruag-c-card__content {
                grid-area: content3;
            }
        }
    }

}



