.ruag-o-tag {
    $tag-height: 22PX;
    $icon-width: 23PX;
    $icon-height: 22PX;

    display: flex;
    height: $tag-height;
    padding: 0 $inuit-global-spacing-unit-factor-0-75;
    border-radius: $icon-height / 2;

    &__text {
        @include inuit-font-size(14px, 22px);
    }

    &__icon {
        height: $icon-height;

        &-outline {
            fill: rgba($color--white, 0);
        }
    }

    &--primary {
        color: $color--white;
        background-color: $color--primary1;

        .ruag-o-tag__icon {
            &-fill {
                fill: $color--white;
            }
        }
    }

    &--favorite {
        position: absolute;
        top: 3PX;
        left: calc(100% - 23px);
        z-index: 10;
        padding: 0;
        transition: padding $global-transition-duration $global-transition-timing;

        &.ruag-o-tag--primary {
            background-color: rgba($color--white, 0);
        }

        .ruag-o-tag__text {
            width: 0;
            overflow: hidden;
            opacity: 0;
            transition-timing-function: $global-transition-timing;
            transition-duration: $global-transition-duration;
            transition-property: width, overflow, opacity;
        }

        .ruag-o-tag__icon {
            &-outline {
                transition: fill $global-transition-duration $global-transition-timing;
                fill: rgba($color--primary1, 0.5);
            }

            &-fill {
                transition: fill $global-transition-duration $global-transition-timing;
            }
        }

        &:hover {
            padding: 0 $inuit-global-spacing-unit-factor-0-75;

            &.ruag-o-tag--primary {
                background-color: $color--primary1;
            }

            .ruag-o-tag__text {
                width: auto;
                overflow: auto;
                opacity: 1;
            }

            .ruag-o-tag__icon {
                &-outline {
                    fill: rgba($color--white, 0);
                }

                &-fill {
                    fill: $color--white;
                }
            }
        }

    }
}
