.ruag-c-jumbotron {
    width: 100%;


    &--image-large {
        height: calc(100vh - 7rem);  // vh - height of jumbotron__footer

        @include mq($from: desktop) {
            height: calc(100vh - 4rem);  // vh - height of jumbotron__footer
        }
    }


    &--image-small,
    &--textonly {
        & ~ .ruag-o-title--page-title {
            @extend .ruag-o-section--has-truncated-predecessor;

            &::before {
                border-right-color: $color--white;
                border-bottom-color: $color--white;
            }
        }
    }


    &--image-small {
        height: 31vh;

        @include mq($from: tablet) {
            height: 28rem;
        }
    }


    &--textonly {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 31vh;
        padding: $inuit-global-spacing-unit-factor-2 0;
        background: $color--porcelain-gray;

        @include mq($from: tablet) {
            min-height: 28rem;
            padding: $inuit-global-spacing-unit-factor-4 0;
        }

        .ruag-c-jumbotron__text {
            padding: 0 10% 0 20PX;  // 10% because of the cut triangle

            @include mq($from: tablet) {
                padding: 0;
            }
        }

        & ~ .ruag-o-title--page-title {
            padding: 0;
        }
    }

    /* stylelint-disable */
    &__inner {
        position: relative;
        width: 100%;
        height: 100%;
    }

    /* stylelint-enable */
    &__image {
        width: 100%;
        height: 100%;

        .media--blazy {
            width: 100%;
            height: 100%;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        video {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }


    &__scroll-button {
        display: block;
        padding: $inuit-global-spacing-unit-factor-3 0;

        @include mq($from: desktop) {
            padding: $inuit-global-spacing-unit-factor-1-5 0;
        }

        color: $color--text;
        font-weight: 200;
        text-decoration: none;
        cursor: pointer;


        &::after {
            display: inline-block;
            margin-left: $inuit-global-spacing-unit-factor-0-5;
            color: $color--monza-red;
            transform: translateY(-2PX);
            transition: transform $global-transition-duration $global-transition-timing;
            content: '\2193';
        }


        &:hover {
            color: $color--monza-red;


            &::after {
                transform: translateY(2PX);
            }
        }

        &:focus {
            outline: 0;
        }
    }
}
