.ruag-o-pagination {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: $inuit-global-spacing-unit-factor-1-5;

    &__navigation {
        display: flex;
        align-items: center;
        justify-content: center;

        &-arrow {
            color: $color--primary1;
            font-size: 1.5rem;

            &--disabled {
                color: $color--loblolly-gray;
            }
        }
    }

    &__pagelinks {
        margin: 0 $inuit-global-spacing-unit-factor-1;

        a {
            color: $color--primary1;
            text-decoration-color: $color--primary1;
        }
    }

    &__pagelink {
        display: inline-block;
        width: 0.7em;
        margin: 0 0.25rem;

        &--active {
            color: $color--text;
        }
    }
}
